import { Switch, FormControlLabel, Typography, makeStyles } from "@material-ui/core";

const useStyles = makeStyles(() => ({
  root: {
    color: 'black',
    marginLeft: 0,
  }
}))

export default function Rebalance(props) {
  const classes = useStyles();

  const { rebalance, setRebalance, setEdited } = props;

  const onChange = (event) => {
    setRebalance(event.target.checked);
    setEdited(true);
    window.localStorage.setItem('rebalance', event.target.checked);
  }

  return (
    <div>
      <FormControlLabel
        className={classes.root}
        control={
          <Switch
            checked={rebalance}
            onChange={onChange}
            color='primary'
          />
        }
        label={<Typography variant='subtitle'>Rebalance?</Typography>}
        labelPlacement="start"
      />
    </div>
  );
}